
import React,{ useState } from 'react';
import { ListItem } from '@material-ui/core';
import { Collapse } from '@material-ui/core';
import {ExpandLess, ExpandMore} from "@material-ui/icons/";
import { ListItemIcon } from '@material-ui/core';
import { ListItemText } from '@material-ui/core';
import { List } from '@material-ui/core';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import ViewModuleIcon from '@material-ui/icons/ViewModule';
const SellSidebarContent = ({navigate,pathname,activeColor,inactiveColor,inactiveTextColor,activeTextColor}) => {
  const [openProductDropdown,setProductDropdownOpen] = useState(false);
  const handleProductDropdownOpen = ()=>{
    setProductDropdownOpen(!openProductDropdown);
  }
  return (
    <>
    <ListItem 
        onClick={handleProductDropdownOpen}
        button>
          <ListItemIcon>
            <ViewModuleIcon />
          </ListItemIcon>
          <ListItemText primary="Sell" />
          {handleProductDropdownOpen ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openProductDropdown} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/sell/list' ? activeColor : inactiveColor, 
              color:  pathname !== '/sell/list' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/sell/list')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="All Sales" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/sell/create' ? activeColor : inactiveColor, 
              color:  pathname !== '/sell/create' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/sell/create')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Add Sales" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/pos/list' ? activeColor : inactiveColor, 
              color:  pathname !== '/pos/list' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/pos/list')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="POS List" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/pos' ? activeColor : inactiveColor, 
              color:  pathname !== '/pos' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/pos')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="POS" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/draft/create' ? activeColor : inactiveColor, 
              color:  pathname !== '/draft/create' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/draft/create')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Add Draft" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/draft/list' ? activeColor : inactiveColor, 
              color:  pathname !== '/draft/list' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/draft/list')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="List Draft" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/quotation/create' ? activeColor : inactiveColor, 
              color:  pathname !== '/quotation/create' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/quotation/create')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Add Quotation" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/quotation/list' ? activeColor : inactiveColor, 
              color:  pathname !== '/quotation/list' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/quotation/list')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="List Quotation" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/sell/return/list' ? activeColor : inactiveColor, 
              color:  pathname !== '/quatation/return/list' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/sell/return/list')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="List Sell Return" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/shipments' ? activeColor : inactiveColor, 
              color:  pathname !== '/shipments' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/shipments')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Shipments" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/discounts' ? activeColor : inactiveColor, 
              color:  pathname !== '/discounts' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/discounts')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Discounts" />
            </ListItem>
          </List>
        </Collapse>
    </>
  );
};

export default SellSidebarContent;