
import React,{ useState } from 'react';
import { ListItem } from '@material-ui/core';
import { Collapse } from '@material-ui/core';
import {ExpandLess, ExpandMore} from "@material-ui/icons/";
import { ListItemIcon } from '@material-ui/core';
import { ListItemText } from '@material-ui/core';
import { List } from '@material-ui/core';
import ArrowRightAltIcon from '@material-ui/icons/ArrowRightAlt';
import ViewModuleIcon from '@material-ui/icons/ViewModule';
const ReportsSidebarContent = ({navigate,pathname,activeColor,inactiveColor,inactiveTextColor,activeTextColor}) => {
  const [openReportDropdown,setReportDropdownOpen] = useState(false);
  const handleReportDropdownOpen = ()=>{
    setReportDropdownOpen(!openReportDropdown);
  }
  return (
    <>
    <ListItem 
        onClick={handleReportDropdownOpen}
        button>
          <ListItemIcon>
            <ViewModuleIcon />
          </ListItemIcon>
          <ListItemText primary="Reports" />
          {handleReportDropdownOpen ? <ExpandLess /> : <ExpandMore />}
        </ListItem>
        <Collapse in={openReportDropdown} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/profit-loss' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/profit-loss' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/profit-loss')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Profit / Loss Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/product-purchase' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/product-purchase' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/product-purchase')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Product Purchase Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/sales-representative' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/sales-representative' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/sales-representative')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Sales Representative Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/register' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/register' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/register')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Register Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/expense' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/expense' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/expense')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Expense Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/sell-payment' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/sell-payment' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/sell-payment')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Sell Payment Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/purchase-payment' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/purchase-payment' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/purchase-payment')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Purchase Payment Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/product-sell' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/product-sell' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/product-sell')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Product Sell Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/items' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/items' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/items')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Items Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/purchaseandsale' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/purchaseandsale' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/purchaseandsale')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Purchase & Sale" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/trending' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/trending' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/trending')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Trending Products" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/stock-adjustment' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/stock-adjustment' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/stock-adjustment')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Stock Adjustment Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/stock' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/stock' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/stock')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Stock Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/supplierandcustomer' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/supplierandcustomer' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/supplierandcustomer')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Supplier & Customer Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/customer-group' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/customer-group' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/customer-group')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Customer Group" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/tax' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/tax' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/tax')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Tax Report" />
            </ListItem>
            <ListItem 
              button 
              style={{ 
              backgroundColor: pathname === '/report/activitylog' ? activeColor : inactiveColor, 
              color:  pathname !== '/report/activitylog' ? inactiveTextColor : activeTextColor,paddingLeft: "4em"}}
              onClick={()=> {return navigate('/report/activitylog')}}>
              <ListItemIcon>
                <ArrowRightAltIcon />
              </ListItemIcon>
              <ListItemText primary="Activity Log" />
            </ListItem>
          </List>
        </Collapse>
    </>
  );
};

export default ReportsSidebarContent;